@import "unset-bootstrap.css";
@import "type-base.css";
@import "color-base.css";
@import "header-footer.css";
@import "module-breadcrumb.css";
@import "override-print.css";
@media(prefers-reduced-motion: reduce) {
    *,
    *:before,
    *:after {
        transition: none !important;
        animation: none !important;
        scroll-behavior: auto !important;
    }
}

body {
    min-width: 320px !important;
    min-height: 600px !important;
    overflow: auto;
}


/* =============================================== Browser Hacks ====================*/


/* IE10+ and Edge hack for sidebar. Position: "sticky" is not supported */

_:-ms-lang(x),
.sidebar {
    position: absolute;
    top: auto;
}


/*Hack for other browsers other than IE 11 and below*/

@supports not (old: ie) {
    .ie-browser-msg {
        display: none;
    }
}


/*Safari (7.1+)*/

_::-webkit-full-page-media,
_:future,
:root .ie-browser-msg {
    display: none;
}


/*Safari (from 6.1 to 10.0)*/

@media screen and (min-color-index:0) and(-webkit-min-device-pixel-ratio:0) {
    .ie-browser-msg {
        display: none;
    }
}


/*Safari (10.1+)*/

@media not all and (min-resolution:.001dpcm) {
    .ie-browser-msg {
        display: none;
    }
}

.ie-browser-msg {
    padding: 1em;
    width: 100%;
    max-width: 100%;
}

.ie-browser-msg a {
    color: var(--stgr)
}